'use client';

import { useState, useTransition, useRef, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import { CornerDownLeft, Loader2, User, Bot, Sparkles } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { useToast } from '@/hooks/use-toast';
import { getInterviewCoachResponse } from '@/lib/actions';
import { Form, FormControl, FormField, FormItem } from '@/components/ui/form';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { cn } from '@/lib/utils';

const chatSchema = z.object({
  message: z.string().min(1, 'Please enter a message.'),
});

type ChatFormData = z.infer<typeof chatSchema>;

type Message = {
  role: 'user' | 'bot';
  text: string;
};

const exampleQuestions = [
  "How should I answer 'Tell me about yourself'?",
  "What are my biggest strengths and weaknesses?",
  "Give me a practice question for a software engineering role.",
];

export function InterviewCoachChatbot() {
  const { toast } = useToast();
  const [isPending, startTransition] = useTransition();
  const [messages, setMessages] = useState<Message[]>([]);
  const scrollAreaRef = useRef<HTMLDivElement>(null);

  const form = useForm<ChatFormData>({
    resolver: zodResolver(chatSchema),
    defaultValues: { message: '' },
  });

  useEffect(() => {
    if (scrollAreaRef.current) {
      scrollAreaRef.current.scrollTo({
        top: scrollAreaRef.current.scrollHeight,
        behavior: 'smooth',
      });
    }
  }, [messages]);

  const handleSubmitMessage = (messageText: string) => {
    if (!messageText) return;

    const userMessage: Message = { role: 'user', text: messageText };
    setMessages((prev) => [...prev, userMessage]);
    form.reset();

    startTransition(async () => {
      const result = await getInterviewCoachResponse(messageText);
      if (result.error) {
        toast({ variant: 'destructive', title: 'Error', description: result.error });
        // Optional: remove user message if bot fails
        setMessages((prev) => prev.slice(0, prev.length - 1));
      } else if (result.data) {
        const botMessage: Message = { role: 'bot', text: result.data.answer };
        setMessages((prev) => [...prev, botMessage]);
      }
    });
  };

  const onSubmit = (data: ChatFormData) => {
    handleSubmitMessage(data.message);
  };
  
  const handleExampleClick = (question: string) => {
    handleSubmitMessage(question);
  }

  return (
    <div className="flex flex-col h-[700px] w-full max-w-2xl mx-auto border rounded-lg shadow-lg">
      <div className="p-4 border-b">
        <h2 className="text-xl font-bold">AI Interview Coach</h2>
        <p className="text-sm text-muted-foreground">Ask me anything about job interviews!</p>
      </div>
      <ScrollArea className="flex-1 p-4" ref={scrollAreaRef}>
        <div className="space-y-4">
          {messages.map((message, index) => (
            <div
              key={index}
              className={cn(
                'flex items-start gap-3',
                message.role === 'user' ? 'justify-end' : 'justify-start'
              )}
            >
              {message.role === 'bot' && (
                <Avatar className="h-8 w-8">
                  <AvatarFallback>
                    <Bot />
                  </AvatarFallback>
                </Avatar>
              )}
              <div
                className={cn(
                  'max-w-xs md:max-w-md lg:max-w-lg p-3 rounded-lg',
                  message.role === 'user'
                    ? 'bg-primary text-primary-foreground'
                    : 'bg-muted'
                )}
              >
                <p className="text-sm whitespace-pre-wrap">{message.text}</p>
              </div>
              {message.role === 'user' && (
                 <Avatar className="h-8 w-8">
                   <AvatarFallback>
                     <User />
                   </AvatarFallback>
                 </Avatar>
              )}
            </div>
          ))}
          {isPending && (
             <div className="flex items-start gap-3 justify-start">
                <Avatar className="h-8 w-8">
                  <AvatarFallback>
                    <Bot />
                  </AvatarFallback>
                </Avatar>
                <div className="bg-muted p-3 rounded-lg">
                    <Loader2 className="h-5 w-5 animate-spin" />
                </div>
             </div>
          )}
        </div>
      </ScrollArea>
      <div className="p-4 border-t">
        {messages.length === 0 && (
          <div className="mb-4 space-y-2">
            <div className='flex items-center gap-2 text-sm text-muted-foreground'>
              <Sparkles className="h-4 w-4" />
              <p>Or try one of these example questions:</p>
            </div>
            <div className="flex flex-wrap gap-2">
              {exampleQuestions.map((q) => (
                <Button key={q} variant="outline" size="sm" onClick={() => handleExampleClick(q)} disabled={isPending}>
                  {q}
                </Button>
              ))}
            </div>
          </div>
        )}
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="flex items-center gap-2">
            <FormField
              control={form.control}
              name="message"
              render={({ field }) => (
                <FormItem className="flex-1">
                  <FormControl>
                    <Input
                      placeholder="e.g., How do I answer 'tell me about yourself'?"
                      {...field}
                      disabled={isPending}
                    />
                  </FormControl>
                </FormItem>
              )}
            />
            <Button type="submit" size="icon" disabled={isPending}>
              {isPending ? <Loader2 className="h-4 w-4 animate-spin" /> : <CornerDownLeft className="h-4 w-4" />}
            </Button>
          </form>
        </Form>
      </div>
    </div>
  );
}
