'use server';

import { analyzeResume as analyzeResumeFlow, AnalyzeResumeOutput } from '@/ai/flows/resume-analysis';
import { personalizedInternshipRecommendations as getRecommendationsFlow, PlatformAssistantInput } from '@/ai/flows/personalized-internship-recommendations';
import { interviewCoach as interviewCoachFlow, InterviewCoachInput } from '@/ai/flows/interview-coach';
import { z } from 'zod';

const resumeSchema = z.object({
  resumeDataUri: z.string().refine(val => val.startsWith('data:application/pdf;base64,'), {
    message: 'Resume must be a PDF file in data URI format.',
  }),
});

export async function handleResumeAnalysis(resumeDataUri: string): Promise<{ data?: AnalyzeResumeOutput; error?: string }> {
  const validation = resumeSchema.safeParse({ resumeDataUri });
  if (!validation.success) {
    return { error: validation.error.flatten().fieldErrors.resumeDataUri?.[0] || "Invalid file format." };
  }

  try {
    const analysis = await analyzeResumeFlow({ resumeDataUri });
    return { data: analysis };
  } catch (error) {
    console.error(error);
    return { error: 'Failed to analyze resume. Please try again.' };
  }
}

const recommendationSchema = z.object({
  message: z.string().min(1, "Please enter a message."),
  history: z.array(z.any()), // Basic validation, can be improved with a Zod schema for history entries
});

export async function getRecommendations(message: string, history: any[]): Promise<{ data?: { recommendations: string; }; error?: string }> {
  const validation = recommendationSchema.safeParse({ message, history });
  if (!validation.success) {
    return { error: validation.error.flatten().fieldErrors.message?.[0] || 'Invalid input.' };
  }
  
  const input: PlatformAssistantInput = {
    message,
    history,
    profile: "Student at State University, 3rd year Computer Science. Skills in React, NodeJS, and Python. Previous internship at a small startup.",
    applicationHistory: "Applied for Software Engineering Intern roles at Google and Facebook.",
  };

  try {
    const recommendations = await getRecommendationsFlow(input);
    return { data: recommendations };
  } catch (error) {
    console.error(error);
    return { error: 'Failed to get recommendations. Please try again.' };
  }
}

const interviewCoachSchema = z.object({
  question: z.string().min(1, 'Question cannot be empty.'),
});

export async function getInterviewCoachResponse(question: string): Promise<{ data?: { answer: string }; error?: string }> {
  const validation = interviewCoachSchema.safeParse({ question });
  if (!validation.success) {
    return { error: validation.error.flatten().fieldErrors.question?.[0] || 'Invalid input.' };
  }

  const input: InterviewCoachInput = { question };

  try {
    const response = await interviewCoachFlow(input);
    return { data: response };
  } catch (error) {
    console.error(error);
    return { error: 'Failed to get a response. Please try again.' };
  }
}
